.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 300px;
    background: #13070c;
    transition: all ease-in-out 0.5s;
    color: #eff9f0;
    font-size: 2em;
}

.sidebar li {
    list-style-type: none;
    text-align: left;
}

@media (max-width: 1199px) {
    .sidebar {
        left: -300px;
    }
}

